* {
  /*border: 1px solid red;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  
}



.App {
  text-align: center;
  height: 800px;
}

.App-header {
  background-color: black;
  height: auto;
  padding: 20px;
  color: white;
  
}

    #firstLine {
      display: -ms-inline-flexbox;
      display: inline-flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-pack: center;
          justify-content: center;
      width: 100%;
      -ms-flex-align: center;
          align-items: center; /* Na výšku */
    }

        .App-logo {
          -webkit-animation: App-logo-spin infinite 10s linear;
                  animation: App-logo-spin infinite 10s linear;
          height: 60px;
          width: 60px;
          border-radius: 50px;
        }

        .App-title {
          font-size: 1.5em;
          width: auto;
          text-align: center;
          padding-left: 10px;
        }

    .App-intro {
      font-size: large;
      width: auto;
      max-width: 470px;
      text-align: center;
      margin: auto;
    }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#main {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  color: green;
  width: 100%;
  height: 80%;
}

    #sideBar {
      height: 100%;
      width: 30%;
    }        
        #searchField {
          padding: 10px;
          height: 40px;
          width: 100%;
        }
    
        #list {
        background-color: black;
        height: 100%;
        margin-top: -15px;
        padding-top: 10px;
        }

    #map {
      height: 100%;
      width: 70%;
    }




